<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddRoleDialog">
            
          </el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="roleList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag>{{ item1.authName }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag type="success">{{ item2.authName }}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="(item3) in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showRightEditDialog(scope.row.roleId)"
              ></el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteRoleById(scope.row.roleId)"
              ></el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showRightDialog(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="roleAddDialogVisible"
      width="30%"
      @close="roleAddDialogClosed"
      append-to-body
    >
      <el-form :model="roleAddForm" ref="roleAddFormRef" label-width="70px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="roleAddForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="roleAddForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="roleAddDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRoleInfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="roleEditDialogVisible"
      width="50%"
      @close="roleEditDialogClosed"
    >
      <el-form :model="roleEditForm" ref="roleEditFormRef" label-width="70px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="roleEditForm.roleName" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="roleEditForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="roleEditDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleInfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="showRightDialogVisible" width="50%"
    @close="resetRightCheckedFileds">
      <el-tree
        :data="rightsList"
        :props="treeProps" show-checkbox node-key="id" default-expand-all
        :default-checked-keys="checkedKeys" ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRights"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      roleList: [],
      roleEditForm: {},
      roleEditDialogVisible: false,
      roleAddDialogVisible: false,
      roleAddForm: {
        roleName: '',
        roleDesc: ''
      },
      showRightDialogVisible: false,
      // 
      rightsList: [],
      // 
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      checkedKeys: [],
      // roleID
      assignRoleId: ''
    }
  },
  created() {
    this.getRoleList()
  },
  methods: {
    async getRoleList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.roleList = res.data
      //   console.log(this.roleList)
    },
    // 
    async showRightEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.roleEditForm = res.data
      this.roleEditDialogVisible = true
    },
    roleEditDialogClosed() {
      this.$refs.roleEditFormRef.resetFields()
    },
    roleAddDialogClosed() {
      this.$refs.roleAddFormRef.resetFields()
    },
    // 
    showAddRoleDialog() {
      this.roleAddDialogVisible = true
    },
    // 
    async editRoleInfo() {
      const { data: res } = await this.$http.put(
        'roles/' + this.roleEditForm.roleId,
        {
          roleId: this.roleEditForm.roleId,
          roleName: this.roleEditForm.roleName,
          roleDesc: this.roleEditForm.roleDesc
        }
      )
      if (parseInt(res.meta.status) !== 200) {
        this.$message.error('')
      }
      // 
      this.roleEditDialogVisible = false
      // 
      this.getRoleList()
    },
    // 
    async deleteRoleById(id) {
      const confirmResult = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // confirmcancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
      this.getRoleList()
    },
    // 
    async addRoleInfo() {
      const { data: res } = await this.$http.post('roles', this.roleAddForm)
      if (parseInt(res.meta.status) !== 201) {
        this.$message.error('')
      }
      // 
      this.roleAddDialogVisible = false
      // 
      this.getRoleList()
    },
    async removeRightById(role, id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.roleId}/rights/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      //   this.getRoleList() 
      role.children = res.data.children
    },
    // 
    async showRightDialog(role) {
      this.assignRoleId = role.roleId
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.rightsList = res.data
      // 
      this.getLeafKeys(role, this.checkedKeys)
      this.showRightDialogVisible = true
    },
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      // Each 
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 
    resetRightCheckedFileds() {
      this.checkedKeys = []
    },
    // 
    async saveRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      console.log(this.assignRoleId)
      const { data: res } = await this.$http.post(`roles/${this.assignRoleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) { return this.$message.error('') }

      this.$message.success('')
      this.getRoleList()
      // 
      this.showRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
