<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-alert
        title=""
        type="info"
        center
        show-icon
        :closable="false"
      >
      </el-alert>
      <!--  -->
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef">
        <!-- tab -->
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          lable-position="top"
          :before-leave="beforTabLeave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <!--  -->
              <el-cascader
                expandTrigger="hover"
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="cateProps"
                @change="handleChange"
                clearable
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- item -->
            <el-form-item
              :label="item.attr_name"
              :key="item.attr_id"
              v-for="item in manyTableData"
            >
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  border
                  :label="val"
                  v-for="(val, i) in item.attr_vals"
                  :key="i"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- action  -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="herderObj"
              :on-success="uploadSuccess"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addForm.goods_introduce">

            </quill-editor>
            <el-button type="primary" class="btnAdd" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" class="previewImg" />
    </el-dialog>
  </div>
</template>

<script>
// lodash_
import _ from 'lodash'
export default {
  data() {
    return {
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        // 
        pics: [],
        goods_introduce: '',
        attrs: []
      },
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      addFormRules: {
        goods_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_price: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_weight: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_number: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_cat: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      cateList: [],
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      uploadURL: 'http://127.0.0.1:8999/upload',
      // 
      herderObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // url
      previewPath: '',
      // 
      previewVisible: false
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('categories')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.cateList = res.data
    },
    // 
    handleChange() {
      // ,
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    beforTabLeave(activeName, oldActiveName) {
      // tabtabname
      if (oldActiveName === '0') {
        // 
        if (this.addForm.goods_cat.length !== 3) {
          this.$message.error('')
          return false
        }
        // else if (this.addForm.goods_name.trim() === '') {
        //   this.$message.error('')
        //   return false
        // } else if (this.addForm.goods_price.trim() === '0') {
        //   this.$message.error('')
        //   return false
        // } else if (this.addForm.goods_weight.trim() === '0') {
        //   this.$message.error('')
        //   return false
        // } else if (this.addForm.goods_number.trim() === '0') {
        //   this.$message.error('')
        //   return false
        // }
      }
    },
    async tabClicked() {
      if (this.activeIndex === '1') {
        if (!this.cateId) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        res.data.forEach(item => {
          var emptyVals = item.attr_vals.length === 0
          item.attr_vals = emptyVals ? [] : item.attr_vals.split(' ')
        })
        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.onlyTableData = res.data
      }
    },
    uploadSuccess(response) {
      // 
      if (response.meta.status !== 200) {
        this.$message.error(response.meta.msg)
      }
      this.addForm.pics.push({ pic: response.data.tmp_path })
      // console.log(this.addForm.pics)
    },
    // 
    handlePreview(file) {
      // 
    // file
      this.previewPath = file.response.data.url
      // 
      this.previewVisible = true
    },
    // 
    handleRemove(file) {
      // fileconsole.log
      // pics
      const filePath = file.response.data.tem_path
      // findIndex
      const index = this.addForm.pics.findIndex(item => item.pic === filePath)
      // 
      this.addForm.pics.splice(index, 1)
    },
    // 
    add() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error('!')

        // addFormgoods_cat
        const form = _.cloneDeep(this.addForm)
        // goods_cat"1,2,3"
        form.goods_cat = form.goods_cat.join(',')
        // attrs
        // manyTableDataattrs
        // 
        this.manyTableData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.addForm.attrs.push(newInfo)
        })
        // 
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        console.log(form)

        // 
        // 
        const { data: res } = await this.$http.post('goods', form)

        if (res.meta.status !== 201) {
          return this.$message.error('')
        }

        this.$message.success('')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.previewImg {
  width: 100%;
}
.btnAdd {
  margin-top: 15px;
}
</style>
